import React from 'react';
import { FormField, InputArea } from '@wix/design-system';
import { BaseInputProps } from './types';
import {
  useEntityPageContext,
  useSelector,
  useWixPatternsContainer,
} from '@wix/patterns';
import { useController } from '@wix/patterns/form';

export interface LongTextProps extends BaseInputProps<string> {}

export const LongText = (props: LongTextProps) => {
  const { field, dataHook } = props;

  const { translate: t, ...container } = useWixPatternsContainer();
  useSelector(() => container.initTask.status);

  const pageState = useEntityPageContext();

  const controller = useController({
    name: field?.id ?? '',
    control: pageState.form.control,
    defaultValue: (pageState.entity as any)?.[field?.id ?? ''],
    rules: {
      validate: (value) => {
        if (
          field.validation?.required &&
          (value === null || value === undefined || value === '')
        ) {
          return t('cairo.fieldValidation.requiredField');
        }

        return true;
      },
    },
  });

  return (
    <FormField
      label={field.displayName}
      required={field.validation?.required}
      status={controller.fieldState.error ? 'error' : undefined}
      statusMessage={controller.fieldState.error?.message}
      dataHook={dataHook}
    >
      <InputArea
        value={controller.field.value}
        onChange={(e) => controller.field.onChange?.(e.target.value)}
        onBlur={controller.field.onBlur}
        ref={controller.field.ref}
        dataHook={`long-text-${field.id}`}
      />
    </FormField>
  );
};
